<template>
  <div>
    <v-container class="sucursal__container">
      <title-ribbon :title="charged === true ? sucursal.Nombre : '...'" />
      <div
        v-if="charged"
        class="sucursal-container__main"
      >
        <v-row class="sucursal-row__img">
          <v-col>
            <v-img
              :src="
                `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/sucursales/${sucursal.CodSucursal}/${sucursal.ImagenPrincipal[0].Imagen}`
              "
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="sucursal-row__horarios">
          <v-col class="col-12 col-md-6 srh-left">
            <splitter-title
              v-bind="{ color: 1, title: 'Horarios de Atención:' }"
            />
            <v-row class="srh-left__datos">
              <v-col class="main_col">
                <div class="srh-left-datos__horarios">
                  <p>
                    {{ ordenaHorarios[0] }} <br>
                    {{ ordenaHorarios[1] }}
                  </p>
                </div>
                <div class="srh-left-datos__main">
                  <div>
                    <ul>
                      <li>
                        <v-icon>mdi-map-marker</v-icon>
                        <span>{{ sucursal.DireccionAbreviada }}</span>
                      </li>
                      <li>
                        <v-img
                          src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/sucursales/metro_xot4gi"
                          max-width="30"
                        />
                        <span>{{ sucursal.DireccionReferencia }}</span>
                      </li>
                      <li>
                        <v-icon>mdi-phone</v-icon>
                        <span><a :href="`tel: ${sucursal.Telefono} `">{{
                          sucursal.Telefono
                        }}</a></span>
                      </li>
                      <li>
                        <v-icon>mdi-email-outline</v-icon>
                        <a :href="`mailto: ${sucursal.Email}`">{{
                          sucursal.Email
                        }}</a>
                      </li>
                    </ul>
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-col>
          <v-col class="col-12 col-md-6 srh-right">
            <iframe
              loading="lazy"
              allowfullscreen
              :src="
                `https://www.google.com/maps/embed/v1/place?q=place_id:${sucursal.GoogleMapPlaceId}&key=AIzaSyBZLWRIjUFiWyWto8aAOlG20eof-E9ySyk`
              "
            />
          </v-col>
        </v-row>
        <splitter v-bind="{ color: 2 }" />
        <v-row>
          <v-col>
            <splitter-title
              v-bind="{
                color: 3,
                title: `Profesionales ${sucursal.Nombre}:`,
              }"
            />
          </v-col>
        </v-row>
        <v-row class="sucursal-row__profesionales">
          <v-col class="col-12 col-md-8 srp_left">
            <v-row>
              <v-col
                v-for="(esp, e) in sucursal.Especialidades"
                :key="e"
                class="srp_left_col col-12 col-md-6 col-lg-4"
              >
                <v-btn
                  style="width: 100%"
                  :to="`/terapeutas/${sucursal.ToLink}/${esp.ToLink}`"
                >
                  {{ esp.NombreCompleto }}
                </v-btn>
              </v-col>
            </v-row>
          </v-col>
          <v-col class="col-12 col-md-1 srp_middle">
            <div class="splitter-sucursal-profesionales" />
          </v-col>
          <v-col class="col-12 col-md-3 srp_right">
            <v-img
              max-width="250"
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/sucursales/reserva_v1iu2r"
              @click="openUrl('https://www.centrodeterapia.cl/horas_online/')"
            />
          </v-col>
        </v-row>
        <splitter v-bind="{ color: 2 }" />
        <v-row class="sucursal-row__photos justify-center">
          <v-col class="col-10 col-md-12 sucursal-row-photos__col">
            <div class="main">
              <v-row class="main_row">
                <v-col
                  v-for="(imagen, i) in sucursal.Imagenes"
                  :key="i"
                  class="col-md-4"
                >
                  <div class="element">
                    <figure>
                      <v-img
                        class="img-hover"
                        :src="
                          `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/sucursales/${sucursal.CodSucursal}/${imagen.Imagen}`
                        "
                      />
                    </figure>
                  </div>
                </v-col>
              </v-row>
            </div>
          </v-col>
        </v-row>
        <splitter-volver />
      </div>
      <div
        v-if="!charged"
        class="loader"
      >
        <v-progress-circular
          :size="70"
          :width="7"
          color="success"
          indeterminate
        />
      </div>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'

  export default {
    name: 'Sucursal',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterTitle: () => import('@/components/web/base/SplitterTitle'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        sucursal: [],
        charged: false,
        ruta: '',
      }
    },
    computed: {
      // ordenaEspecialidades () {
      //   // Deja Terapia Ocupacional al último
      //   const arrayEsp = this.especialidadesPaginaSucursales
      //   const indexTerapiaOcupacional = arrayEsp.findIndex(
      //     esp => esp.CodSiat === 85,
      //   )

      //   if (indexTerapiaOcupacional) {
      //     const [terapiaOcupacional] = arrayEsp.splice(indexTerapiaOcupacional, 1)
      //     arrayEsp.push(terapiaOcupacional)
      //   }

      //   return arrayEsp
      // },
      ordenaHorarios () {
        // Separa horarios semana y sábado
        return this.sucursal.Horario.split(',')
      },
      getTitleByRoute () {
        return 'asas'
      },
    },
    watch: {
      '$route.params.toLink': {
        handler: function (toLink) {
          this.getSucursal(toLink)
        },
      },
    },
    created () {
      this.getSucursal(this.$route.params.toLink)
    },
    methods: {
      getSucursal (toLink) {
        const body = {
          addImagenes: true,
          addImagenPrincipal: true,
          addEspecialidades: true,
          addRangosEtarios: true,
        }
        Vue.axios
          .post(`/api/sucursales/${toLink}`, body)
          .then(response => {
            this.sucursal = response.data.result[0]
            this.charged = true
          })
          .catch(err => {
            console.log(err)
          })
      },
      openUrl (url) {
        window.open(url, '_blank')
      },
    },
    metaInfo: {
      title: 'Sucursales',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
